.section-large-text {
    position: relative;
    background: url("../../accest/images/section-b.jpg") bottom center/cover no-repeat;
    height: 600px;
}

.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: var(--overlay-color);
}

.section-large-text-inner {
    color: #fff;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: auto;
    max-width: 860px;
    padding: 5rem 0;
}

.section-large-text-inner h2 {
    font-size: 5rem;
    margin-top: 1rem;
}

.section-large-text-inner h3 {
    font-size: 2rem;
}

.section-large-text-inner p {
    font-size: 1.5rem;
    margin-top: 1rem;
}



@media (max-width: 768px) {
    .section-large-text-inner h2 {
        font-size: 3rem;
    }

    .section-large-text-inner h3 {
        font-size: 1.5rem;
    }

    .section-large-text-inner p {
        font-size: 1.25rem;
    }
}